x;padding:7px 20px;display:inline-block;box-shadow:0px 0px 33px 0px rgba(0, 0, 0, 0.75)}.stopwatch .stopwatch__menu .stopwatch__button{background-color:transparent;border:none;outline:none}.stopwatch .stopwatch__menu .stopwatch__button:disabled{opacity:.3!important}.stopwatch .stopwatch__menu .stopwatch__button i{cursor:pointer;-webkit-transition:.3s all;transition:.3s all}.stopwatch .stopwatch__menu .stopwatch__button i:before{font-size:35px;color:#3e98c7;margin:0}.stopwatch .stopwatch__menu .stopwatch__button i:hover{text-shadow:0 0 11px rgba(0, 0, 0, 0.75)}
